@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed);
body {
  margin: 0;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
}

*:focus {
  outline: none;
}
.Content {
  margin-top: 72px;
  margin-bottom: 72px;
}

.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 70%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 15%;
    top: 30%;
    box-sizing: border-box;
    -webkit-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

@media (min-width: 600px) {
    .Modal {
        width: 700px;
        left: calc(50% - 250px);
    }
}

.Backdrop {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
}

@media (min-width: 500px) {
    .Backdrop {
        display: none;
    }
}

main {
  max-width: 960px;
  margin: 0 auto;
}

.fix {
  position: fixed;
  top: 10px;
  right: 10px;
}

h2 {
  margin-bottom: 20px;
}

article {
  padding: 10px 20px;
  background-color: rgb(240, 240, 240);
}

h1 {
  font-size: 3rem;
}

h2 {
  background-color: #007072;
  color: white;
  margin-top: 20px;
}

article h2 {
  padding-left: 30px;
}

p {
  margin: 10px 0;
}

@media only screen and (max-width: 600px) {
  a {
    display: block;
  }
}
